* {
    font-family: sans-serif;
}

body {
    padding: 60px;
}

button {
    block-size: auto;
}

input {
    color: gray;
    font-size: 1.2rem;
    width: 100%;
}

.card {
    background: wheat;
    border: 1px solid green;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
    max-height: 170px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}